<template>
	<div class="white-box">
		<h5>
			Person {{ name }}
			<template v-if="initiator">(initiator)</template>
		</h5>
		<TechStatus :techStatus="techStatus"/>
		<div>
			signaling data: <textarea class="status">{{ signalingData }}</textarea>
		</div>

		<fieldset>
			<!-- Connect to -->
			<input type="text" v-model="signalToInput" @keydown.enter="signalTo()" placeholder="Target's signaling data">
			<button @click="signalTo()">Connect</button>
		</fieldset>

		<div>
			Data received: <code>{{ dataReceived }}</code>
		</div>
	</div>
</template>

<script>
import TechStatus from './TechStatus'
import CopyButton from '../lib/CopyButton'

export default {
	name: 'Person',
	props: {
		name: {
			type: String,
			default: 'unnamed'
		},
		initiator: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			status: '-',
			p: undefined,
			signalingData: [], // ie. own address
			signalToInput: '',
			dataReceived: 'nothing'
		}
	},
	computed: {
		techStatus() {
			return {
				SimplePeer: window.SimplePeer,
				Instance: this.p,
				SignalingData: this.signalingData
			}
		}
	},
	methods: {
		init() {
			this.initiator
				? this.p = new SimplePeer( {initiator: true} )
				: this.p = new SimplePeer()

			// When {initiator: true}, 
			// 	SimplePeer starts to query TURN servers etc. for client's addresses,
			// 	aka SignalingData (offer)
			// The remote client {initiator: false} can then use this address 
			// 	to signal to client, via simplePeer.signal(signalingData)
			this.p.on('signal', data => {
				// console.log(`signal fired from ${this.name}.`)
				this.signalingData.push(data)
			})

			this.p.on('connect', () => {
				console.log('connected', this.name)
				this.p.send(`Hello, this is ${this.name}.`)
			})
			this.p.on('data', data => {
				console.log(`data fired from ${this.name}.`)
				// got a data channel message
				this.dataReceived = data
			})
			this.p.on('error', error => {
				console.error('Error', error)
			})
		},
		signalTo() {
			var sig = JSON.parse(this.signalToInput)
			// check if valid
			if (!sig || !(sig instanceof Array)) {
				console.error(`Target's signaling data invalid`)
				return
			}
			// 
			sig.forEach(s => {
				this.p.signal(s)
			})
		}
	},
	mounted() {

		// Init
		this.init()
	},
	components: {
		TechStatus,
		CopyButton
	}
}
</script>

<style scoped>
.status {
	display: inline-block;
	vertical-align: middle;

	max-width: 50ch;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
</style>